import Vue from 'vue';

import App from './App.vue';
import store from './store';

const vm = new Vue({
  el: '#app',
  store,
  render: h => h(App),
  methods: {
    initMessage(){
      if (typeof window !== 'undefined') {
        window.addEventListener('message', e => {
          console.log("%c Log: %c initMessage -> %c e ", ...Array(3).fill("color:aqua"), e);
          if (e && e.data) {
            store.dispatch('setAnimationData', e.data);
          }
        });
      }
    },
  },
});

vm.initMessage();



